.main-header {
  position: relative;
  min-width: 320px;
  padding: 10px 20px 17px 20px;
  margin: 0 auto;
  justify-content: space-between;
  max-width: 1220px;

  @media (min-width: $tablet-width) {
    padding: 10px 30px 0 30px;
    display: flex;
    align-items: center;
  }

  @media (min-width: $desktop-width) {
    padding: 69px 110px 32px 110px;
  }
}
